import { createStore, applyMiddleware, combineReducers, compose } from 'redux';
import { reducerWord, reducerUser } from './web/store';

export function configureStore () {
  let store = createStore(
    combineReducers({ word: reducerWord, user: reducerUser }),
    {
      word:{
        words: [],
        wordsDate: {},
      },
      user:{
        isAdmin: false,
      }
    },
  );

  // console.log('store', store);
  window.store = store;

  // store.subscribe(() => {
  //   let {number} = store.getState();
  //   saveState({
  //     number
  //   });
  // });

  return store;
}


