
export const SHOW_NEWS = 'SHOW_NEWS';
export const HIDE_NEWS = 'HIDE_NEWS';
export const NUMBER_SUB = 'NUMBER_SUB';
export const NUMBER_ADD = 'NUMBER_ADD';
export const TEST_ADD = 'TEST_ADD';

export const now = () => (new Date()).getTime();

export function subNumber(number) {
  return {
    type: NUMBER_SUB,
    payload: {
      number
    }
  };
}

export function addNumber(number) {
  return {
    type: NUMBER_ADD,
    payload: {
      number
    }
  };
}

export function addTest(number) {
  return {
    type: TEST_ADD,
    payload: {
      number
    }
  };
}



export function showNews() {
  return {
    type: SHOW_NEWS
  };
}

export function hideNews() {
  return {
    type: HIDE_NEWS
  };
}


export const mapDispatchToProps = (dispatch) => ({
  addNumber(num) {
    dispatch(addNumber(num));
  },
  subNumber(num) {
    dispatch(subNumber(num));
  },
  showNews() {
    dispatch(showNews());
  },
  hideNews() {
    dispatch(hideNews());
  },
  addTest(num){
    dispatch(addTest(num));
  }

});



