
export const USER_ROLE = 'USER_ROLE';

export function setPermission(isAdmin) {
  return {
    type: USER_ROLE,
    payload:{
        isAdmin
    }
  };
}


export const mapDispatchToPropsUser = (dispatch) => ({
  setPermission(isAdmin) {
    dispatch(setPermission(isAdmin));
  },
});



