
export const WORD_ADD = 'WORD_ADD';
export const WORD_UPDATE = 'WORD_UPDATE';
export const WORD_DELETE = 'WORD_DELETE';
export const WORD_LIST = 'WORD_LIST';
export const WORD_LIST_BY_DATE = 'WORD_LIST_BY_DATE';

export function updateWord(id, word) {
  return {
    type: WORD_UPDATE,
    payload: {
      id,
      word
    }
  };
}

export function addWord(word) {
  return {
    type: WORD_ADD,
    payload: {
      word
    }
  };
}

export function listWord(words) {
  return {
    type: WORD_LIST,
    payload: {
      words
    }
  };
}

export function listWordByDate(wordsDate) {
  return {
    type: WORD_LIST_BY_DATE,
    payload: {
      wordsDate
    }
  };
}

export function deleteWord(id) {
  return {
    type: WORD_DELETE,
    payload:{
        id
    }
  };
}


export const mapDispatchToProps = (dispatch) => ({
  addWord(word) {
    dispatch(addWord(word));
  },
  updateWord(id, word) {
    dispatch(updateWord(id, word));
  },
  listWord(words) {
    dispatch(listWord(words));
  },
  listWordByDate(wordsDate) {
    dispatch(listWordByDate(wordsDate));
  },
  deleteWord(id){
    dispatch(deleteWord(id));
  }

});



