import * as actions from "./number";
import * as actionsWord from "./word";
import * as actionsUser from "./user";

export function reducerNumber(state, action) {
  switch (action.type) {
    case actions.NUMBER_SUB: {
      // let track = state.filter(t => t.id===action.payload.trackId)
      console.log(state)
      return Object.assign({}, state, {
        number: state.number - action.payload.number
      })
    }
    case actions.TEST_ADD: {
      console.log(state.test, action.payload)
      return Object.assign({}, state, {
        test: state.test + action.payload.number
      })
    }
    case actions.NUMBER_ADD: {
      // let track = state.filter(t => t.id===action.payload.trackId)
      console.log( Object.assign({}, state, {
          number: state - action.payload.number
        }))
      return Object.assign({}, state, {
        number: state.number + action.payload.number
      })

    }
  }
  return state;
}

export function reducerWord(state = {}, action) {
  switch (action.type) {
    case actionsWord.WORD_ADD: {
      let state2 = [...state.words]
      state2.unshift(action.payload.word)

      return Object.assign({}, state, {
        words: state2
      })
    }

    case actionsWord.WORD_UPDATE: {
      let index = state.words.findIndex((x, k) => Object.keys(x)[0] == action.payload.id);
      let state2 = {...state};
      state2.words[index] = {[action.payload.id] : action.payload.word}
      return state2;
    }

    case actionsWord.WORD_LIST: {
      return Object.assign({}, state, {
        words: action.payload.words
      })
    }

    case actionsWord.WORD_LIST_BY_DATE: {
      return {...state, wordsDate: action.payload.wordsDate}
    }

    case actionsWord.WORD_DELETE: {
      let index = state.words.findIndex((x, k) => Object.keys(x)[0] == action.payload.id);
      let state2 = {...state};
      state2.words.splice(index, 1);
      return state2;
    }
  }
  return state;
}

export function reducerUser(state = {}, action) {
  switch (action.type) {
    case actionsUser.USER_ROLE: {
      return Object.assign({}, state, {
        isAdmin: action.payload.isAdmin
      })
    }
  }

  return state;
}



