import { h, Component } from 'preact';
import { route } from 'preact-router';
import * as number from '../store/number';
import { connect } from 'preact-redux';
import { mapDispatchToPropsUser } from '../store/user';
import styles from '../../styles/sass/header.scss';
const { firebase } = require('../../firebase');

//redur
const mapStoreToProps = (state, props) => ({
  isAdmin: state.user.isAdmin,
});

@connect(mapStoreToProps, mapDispatchToPropsUser)
export default class Header extends Component {
  componentDidMount() {

  }

  handleClick(){
    let x = document.getElementById("myLinks");
    if (x.style.display === "block") {
      x.style.display = "none";
    } else {
      x.style.display = "block";
    }
  }

  logout = () =>{
    firebase.auth().signOut().then(function() {
      console.log('Sign-out successful.')
      auth.isAdmin = false
      localStorage.setItem('u_i_a', false)
      route('/', true)
    }).catch(function(error) {
      // An error happened.
    });
  }

  render(props) {
    console.log(auth.isAdmin, this.props.isAdmin)
    return (
      <header>
        <div className="topnav">
          <a href="/" className="logo active">
            <img src="/assets/icon.png" width="30" height="30" />
          </a>

          <div id="myLinks">
            <a href="#news">News</a>
            <a href="#contact">Contact</a>
            <a href="#about">About</a>
          </div>

          { (auth.isAdmin || this.props.isAdmin) && (
            <a href="javascript:void(0)" className="logout" onClick={this.logout} >
              <span> Logout </span>
            </a>
          )}

          { !auth.isAdmin && !this.props.isAdmin && (
            <a href="/login" className="login" >
              <span> Login </span>
            </a>
          )}

          { (auth.isAdmin || this.props.isAdmin) && (
            <a href="/create" className="btn-action" >
              <span> + </span>
            </a>
          )}
          <a href="javascript:void(0);" className="icon" onClick={this.handleClick}>
            <img src="/images/bars.svg" width="20px"/>
          </a>
        </div>
      </header>
    );
  }
}


