import { Router, route } from 'preact-router';
import { h, Component } from 'preact';
import AsyncRoute from 'preact-async-route';
import { Provider } from 'preact-redux';

import styles from './styles/css/index.css';

import Header from './web/components/Header';
import Footer from './web/components/Footer';

const { db, firebase } = require('./firebase');
const { configureStore } = require('./configureStore');

import { mapDispatchToProps } from './web/store/word';
import { mapDispatchToPropsUser }  from './web/store/user';

let store = configureStore();
// end

window.db = db
//axios.defaults.baseURL = $("meta[name='version-api']").data('version-api')
//window._csrf = $("meta[name='csrf']").data('csrf')

/** @jsx h */
window.auth ={
  isAdmin: JSON.parse(localStorage.getItem('u_i_a')) || false
}

function appendLeadingZeroes(n){
  if(n <= 9){
    return "0" + n;
  }
  return n
}

window.dateNow = function(){
  var current_datetime = new Date()
  var formatted_date = current_datetime.getFullYear() +
      "-" +
      appendLeadingZeroes(current_datetime.getMonth() + 1) +
      "-" + appendLeadingZeroes(current_datetime.getDate()) +
      " " + appendLeadingZeroes(current_datetime.getHours()) +
      ":" + appendLeadingZeroes(current_datetime.getMinutes()) +
      ":" + appendLeadingZeroes(current_datetime.getSeconds())
  return formatted_date
}

export default class Main extends Component {
  constructor(props){
    super(props)
    this.state = {
    }
  }

  componentDidMount(){
    firebase.auth().onAuthStateChanged((user) => {
      if (user && user.email) {
        // User is signed in.
        this.getUserFromEmail(user.email);
      } else {
        // User is signed out.
        localStorage.setItem('u_i_a', false)
        mapDispatchToPropsUser(store.dispatch).setPermission(false)
      }
    });
    this.runFirebaseFirestore()
  }

  getUserFromEmail(email){
    db.collection("users").doc(email).get().then((querySnapshot) => {
      let data = querySnapshot.data()
      let isAdmin = data && data.isAdmin ? true : false
      localStorage.setItem('u_i_a', isAdmin)
      auth.isAdmin = isAdmin

      mapDispatchToPropsUser(store.dispatch).setPermission(isAdmin)

    })
  }

  runFirebaseFirestore(){
    let words = [];

    //get list word
    // db.collection("words").orderBy("created_at", "desc").get().then((querySnapshot) => {
    //   if(querySnapshot.size){
    //     querySnapshot.forEach((doc) => {
    //       // doc.data() is never undefined for query doc snapshots
    //       words.push({ [doc.id] : doc.data() })
    //     })

    //     console.log('int', words)

    //     mapDispatchToProps(store.dispatch).listWord(words)
    //   }
    // })

    //catch event
    db.collection("words").orderBy("created_at", "asc").onSnapshot((snapshot) => {
      snapshot.docChanges().forEach((change) => {
        if (change.type === "added") {
            var child = {[change.doc.id]: change.doc.data()}
            mapDispatchToProps(store.dispatch).addWord(child)
        }
        if (change.type === "modified") {
            var child = {[change.doc.id]: change.doc.data()}
            mapDispatchToProps(store.dispatch).updateWord(change.doc.id, change.doc.data())
        }
        if (change.type === "removed") {
          mapDispatchToProps(store.dispatch).deleteWord(change.doc.id)
        }
      });
    });

  }


  runFirebaseRealtimeDb(){
    db.ref('words').once('value').then((snap) => {
      //store.dispatch(listWord(snap.val().all));
      //let words = new Map(Object.entries(snap.val().all))
      let words = snap.val().all;

      let result = Object.keys(words).map(function (key) {
         return { [key]: words[key] };
      });

      mapDispatchToProps(store.dispatch).listWord(result)
      mapDispatchToProps(store.dispatch).listWordByDate(snap.val().date)
    })

    db.ref('words').child('all').on("child_added", function(snapshot) {
      var child = {[snapshot.key]: snapshot.val()}
      mapDispatchToProps(store.dispatch).addWord(child)
    });
  }

  handleRoute = async (e) => {
    switch (e.url) {
      case '/create':

        if (!auth.isAdmin) route('/login', true);
        break;
    }
  };

  render (){
    return (
      <Provider store={store}>
          <Header />

          <Router onChange={this.handleRoute}>
              <AsyncRoute
                  path="/"
                  getComponent={ () => import('./web/components/home').then(module => module.default) }
                  loading={ () => <div>loading...</div> }
              />

              <AsyncRoute
                  path="/about"
                  getComponent={ () => import('./web/components/about').then(module => module.default) }
                  loading={ () => <div>loading...</div> }
              />

              <AsyncRoute
                  path="/create"
                  getComponent={ () => import('./web/components/home/create').then(module => module.default) }
                  loading={ () => <div>loading...</div> }
              />

              <AsyncRoute
                  path="/login"
                  getComponent={ () => import('./web/components/user/login').then(module => module.default) }
                  loading={ () => <div>loading...</div> }
              />

          </Router>

          <Footer />
      </Provider>
  )}
};

